
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

#todoInput {
	background: #f5f5f5;
	-webkit-box-shadow: 4px 4px 3px #c5c5c5;
	        box-shadow: 4px 4px 3px #c5c5c5;
	width: 90%;
	padding: 0;
	padding-top: 15px;
	padding-bottom: 15px;
	margin: 0 auto;
	margin-bottom: 4%;
}
#postDate{
	position: absolute; 
	left: 8%;
	font-size: 10pt;
}
#dueDate{
	position: absolute; 
	right: 8%;
	font-size: 10pt;
}
#text{
	margin-top:4%;
}
ul {
	list-style-type: none;
	padding: 0;
	margin: 0 auto;
	width: 90%;
}
li {
	background: #f5f5f5;
	-webkit-box-shadow: 4px 4px 3px #c5c5c5;
	        box-shadow: 4px 4px 3px #c5c5c5;
	padding: 20px;
	margin-bottom: 1%;
	padding-bottom: 40px;
	overflow-wrap: break-word;
}
